* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #56ccf2;
  background: -webkit-linear-gradient(to right, #2f80ed, #56ccf2);
  color: #212121;
  background: linear-gradient(to right, #2f80ed, #56ccf2);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  background: #c31432;
  background: -webkit-linear-gradient(to left, #240b36, #c31432);
  background: linear-gradient(to left, #240b36, #c31432);
  justify-content: center;
  padding: 15px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: 0 5px 5px rgba(33, 33, 33, .24);
}

.input {
  border-style: none;
  border-radius: 2px 0 0 2px;
  padding: 10px;
}

.submitBtn {
  min-width: 30px;
  cursor: pointer;
  border-style: none;
  border-radius: 0 2px 2px 0;
  padding: 3px;
}

.gallery {
  grid-gap: 10px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  padding: 30px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.thumb {
  height: 200px;
  width: 100%;
}

.img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
  display: block;
}

.info {
  justify-content: space-between;
  align-items: flex-end;
  gap: 15px;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  font-size: small;
  display: flex;
}

.footer {
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
  display: flex;
}

.load-more {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #63a736;
  border-style: none;
  border-radius: 5px;
  padding: 15px 20px;
  font-size: medium;
  font-weight: 500;
}

